<template>
	<div class="myBody">
		<div class="bg">
			<img src="../assets/bg_2.jpg" alt=""></div>
		<div class="content">
			<div class="title">
				<h2>党   员   选   择</h2>
				<br>
			</div>
			<div class="mainContent">
				<div class="contentBox">
					<div class="depatementTable">
						<v-table is-horizontal-resize is-vertical-resize style="width:100%" :height="410" fixed :columns="columns" :table-data="tableData" :show-vertical-border="true" row-hover-color="#eee" row-click-color="#edf7ff" :row-click="choose"></v-table>
						<p class="chooseText">
							当前选择 : {{nowChoose.Name}}
						</p>
					</div>
				</div>
				<div class="contentBox">
					<div class="contentBox_2">
						<div class="message">
							<p>
								<span>党员 :</span><span>{{nowChoose.Name}}</span>
							</p>
							<p>
							</p>
							<p>
								<span> 密码 :</span><input type="password" :value="password" />
							</p>
							<br>
						</div>
						<div class="buttonBox">
							<ul class="buttonBoxBtn">
								<button @click="chooseNum(1) ">
									<div>1</div>
								</button>
								<button @click="chooseNum(2)">
									<div>2</div>
								</button>
								<button @click="chooseNum(3)">
									<div>3</div>
								</button>
								<button @click="chooseNum(4)">
									<div>4</div>
								</button>
								<button @click="chooseNum(5)">
									<div>5</div>
								</button>
								<button @click="chooseNum(6)">
									<div>6</div>
								</button>
								<button @click="chooseNum(7)">
									<div>7</div>
								</button>
								<button @click="chooseNum(8)">
									<div>8</div>
								</button>
								<button @click="chooseNum(9)">
									<div>9</div>
								</button>
								<button @click="chooseNum('X')">
									<div>X</div>
								</button>
								<button @click="chooseNum(0)">
									<div>0</div>
								</button>
								<button @click="chooseNum(-1)">
									<div>
										<-</div>
								</button>
							</ul>
							</div>
							<div class="bottom-button">
								<div>
									<button @click="next('makeAnswer')" class="button-1">确定</button>
								</div>
								<div>
									<router-link to="/choiceDepartment" class="button-1">返回</router-link>
								</div>

							</div>
						</div>

					</div>
				</div>

			</div>

		</div>
</template>

<script>
	export default {
		name: "choicePeople",
		methods: {
			choose(rowIndex, rowData, column) {
				this.nowChoose = rowData;
				this.password = '';
			},
			chooseNum(num) {
				if(num != -1) {
					this.password += num;
				} else if(num == -1) {
					this.password = this.password.slice(0, this.password.length - 1);
				}
			},
			next(url) {
				if(this.nowChoose.PartyMemberID != -1) {
					var data = {
						a: this.nowChoose.PartyMemberID,
						b: this.password
					};
					console.log('------data------'+JSON.stringify(data)); //控制台中打印相关信息
					this.$ajax({
							method: "get",
							url: "http://202.206.249.16:8080/dxpj/hp/judge_partyMember_byMemberId?a=" +
								data.a +
								"&b=" +
								data.b
						})
						.then(res => {
							//info

							res = res.data;

							if(res.status.code == 100) {
								//            alert("验证成功");
								this.$router.push({ //传参数
									name: url,
									params: {
										PartyMemberID: this.nowChoose.PartyMemberID,
										Name: this.nowChoose.Name,
										PartyMemberLableName: this.nowChoose.PartyMemberLableName,
										PartyPosition: this.nowChoose.PartyPosition
									}
								});
							} else{
								this.password = '';
								alert(res.status.msg);
							}								
						})
						.catch(function(err) {
							console.log(err);
						});
				} else {
					alert("无数据访问！");
				}
			},
			listUpdate(list) {
				this.tableData = list;
			}
		},
		created: function() {
			var id = this.$route.params.PartyBranchID; //接受参数PartyBranchID  https://www.cnblogs.com/lulianlian/p/7682790.html

			this.$ajax({
					method: "get",
					url: "http://202.206.249.16:8080/dxpj/hp/get_partyMember_byPartyId/" + id
				})
				.then(res => {
					//info

					res = res.data;

					if(res.status.code == 100) {
						var list = res.info;
					} else if(res.status.code == 104) {
						alert(res.status.msg);
						var list = [{
							PartyMemberID: "-1",
							PartyMemberLableName: "暂无",
							Name: "暂无",
							PartyPosition: "暂无"
						}];
					}

					this.listUpdate(list);
				})
				.catch(function(err) {
					console.log(err);
				});
		},
		data() {
			return {
				table_height: "60",
				password: "",
				nowChoose: {
					Name: "未选择",
					PartyPosition: "未选择",
					PartyMemberID: "-1"
				},
				tableData: [{
						PartyMemberID: "1",
						Name: "test1",
						Sex: "男",
						Birthday: "1984-8-20",
						PartyMemberLableName: "中共........",
						PartyPosition: "党员"
					},
					{
						PartyMemberID: "2",
						Name: "test1",
						Sex: "男",
						Birthday: "1988-1-20",
						PartyMemberLableName: "中共........",
						PartyPosition: "党员"
					},
					{
						PartyMemberID: "3",
						Name: "test1",
						Sex: "男",
						Birthday: "1981-8-20",
						PartyMemberLableName: "中共........",
						PartyPosition: "党员"
					},
					{
						PartyMemberID: "4",
						Name: "test1",
						Sex: "女",
						Birthday: "1986-5-20",
						PartyMemberLableName: "中共........",
						PartyPosition: "党员"
					},
					{
						PartyMemberID: "5",
						Name: "test1",
						Sex: "男",
						Birthday: "1984-8-20",
						PartyMemberLableName: "中共........",
						PartyPosition: "党员"
					}
				],
				columns: [{
						field: "PartyMemberID",
						title: "党员编号",
						width: 100,
						titleAlign: "center",
						columnAlign: "center",
						isResize: true
					},
					{
						field: "Name",
						title: "姓名",
						width: 100,
						titleAlign: "center",
						columnAlign: "center",
						isResize: true
					},
					{
						field: "Sex",
						title: "性别",
						width: 100,
						titleAlign: "center",
						columnAlign: "center",
						isResize: true
					},

					{
						field: "Birthday",
						title: "生日",
						width: 100,
						titleAlign: "center",
						columnAlign: "center",
						isResize: true
					},
					{
						field: "PartyBranchName",
						title: "支部名称",
						width: 400,
						titleAlign: "center",
						columnAlign: "center",
						isResize: true
					}, ,
					{
						field: "PartyMemberLableName",
						title: "类别",
						width: 250,
						titleAlign: "center",
						columnAlign: "center",
						isResize: true
					}
				]
			};
		}
	};
</script>

<style scoped>
	.contentBox_2 {
		width: 60%;
	}
	
	.chooseText {
		float: left;
		font-size: 28px;
	}
	
	.depatementTable {
		width: 90%;
		height: 100%;
	}
	
	.bottom-button {
		top: 75% !important;
		left: 40%;
	}
	
	buttonBoxBtn {
		width: 90%;
		margin-top: 10px;
		margin-left: 10px;
		border: 1px solid rgb(204, 204, 204);
		padding: 25px 15px;
	}
	/*li*/
	.buttonBoxBtn button {
		width: 33%;
		float: left;
		text-align: center;
		margin-bottom: 10px;
		background-color: orange;
		border: 2px solid #fff*;
		/*#fff*/
	}
	
	.buttonBoxBtn button div {
		width: 50%;
		height: 47px;
		line-height: 46px;
		background-color: orange;
		/*border: 2px solid #fff;*/
		text-align: center;
		margin: auto;
		font-size: 24px;
	}
	
	.buttonBoxBtn::after {
		content: "";
		display: block;
		clear: both;
	}
	
	.contentBox:nth-of-type(1) {
		width: 60%;
		height: 100%;
		box-sizing: border-box;
	}
	
	.contentBox:nth-of-type(2) {
		width: 36%;
		height: 100%;
		box-sizing: border-box;
	}
	
	.mainContent {
		height: 60%;
	}
	
	.message {
		text-align: left;
		font-size: 24px;
	}
	
	.message p {
		margin-bottom: 7px;
	}
	
	.message p span:nth-of-type(1) {
		display: inline-block;
		width: 30%;
		box-sizing: border-box;
	}
	
	.message p span:nth-of-type(2) {
		display: inline-block;
		width: 70%;
		box-sizing: border-box;
	}
	
	.message p input {
		width: 70%;
		height: 30px;
		padding: 0 10px;
		box-sizing: border-box;
	}
</style>


